@font-face {
    font-family: 'bebas_thin';
    src: url('/static/fonts/bebas/bebasneue_thin-webfont.eot');
    src: url('/static/fonts/bebas/bebasneue_thin-webfont.eot?#iefix') format('embedded-opentype'),
         url('/static/fonts/bebas/bebasneue_thin-webfont.woff2') format('woff2'),
         url('/static/fonts/bebas/bebasneue_thin-webfont.woff') format('woff'),
         url('/static/fonts/bebas/bebasneue_thin-webfont.ttf') format('truetype'),
         url('/static/fonts/bebas/bebasneue_thin-webfont.svg#bebas_neuethin') format('svg');
    font-weight: normal;
    font-style: normal;
}

